@import url(https://fonts.googleapis.com/css?family=Sacramento|Snippet&display=swap);
@import url(https://fonts.googleapis.com/css?family=Liu+Jian+Mao+Cao&display=swap);
body {
  margin: 0;
  font-family: 'Snippet', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
  font-family: 'Sacramento', cursive !important;
}

html {
  scroll-behavior: smooth;
}

.slide-in {
  -webkit-animation: slide-in-x-animation .5s forwards;
          animation: slide-in-x-animation .5s forwards;
}

.fade-in {
  -webkit-animation: fade-in-animation .5s forwards;
          animation: fade-in-animation .5s forwards;
}

.slide-down {
  -webkit-animation: slide-in-y-animation .5s forwards;
          animation: slide-in-y-animation .5s forwards;
}

@-webkit-keyframes fade-in-animation {
  to {
    opacity: 1;
  }
}

@keyframes fade-in-animation {
  to {
    opacity: 1;
  }
}

@-webkit-keyframes slide-in-x-animation {
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slide-in-x-animation {
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes slide-in-y-animation {
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-in-y-animation {
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}


.home {
  min-height: 100vh;
  padding-bottom: 5rem; }
  .home .profile {
    margin-top: -25vh;
    -webkit-transform: translateY(-50vh);
            transform: translateY(-50vh); }
    .home .profile__arrow-text {
      font-family: 'Liu Jian Mao Cao', cursive;
      top: -80px;
      right: -120px; }
    .home .profile__arrow {
      top: -100px;
      right: -200px; }
  .home .introduction {
    opacity: 0;
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px); }
  .home #magic-cloud {
    top: -10px;
    width: 150px;
    height: 80px; }
    .home #magic-cloud + h1 {
      z-index: -2; }
    .home #magic-cloud:before {
      content: '';
      position: absolute;
      right: 70px;
      top: 0;
      height: 80px;
      width: 100vw;
      background-color: white;
      z-index: -1; }

.name-tag {
  font-size: .8rem; }
  .name-tag__portrait {
    border-radius: 100%; }

.nav .nav-item {
  -webkit-transform: translateX(-120vw);
          transform: translateX(-120vw); }
  .nav .nav-item .nav-link {
    font-family: 'Sacramento', cursive; }

.hero {
  height: 50vh; }
  .hero__bg {
    background-image: url(/static/media/sun-sky.742a6cd9.svg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%; }
  .hero__overlay {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(transparent), to(white));
    background: linear-gradient(transparent, transparent, white); }

.about {
  padding-bottom: 5rem; }
  .about .curly {
    stroke-dasharray: 1427px;
    stroke: black;
    stroke-width: 10px;
    stroke-linejoin: round;
    stroke-linecap: round; }

.projects {
  padding-bottom: 5rem; }
  .projects .curly {
    stroke-dasharray: 1427px;
    stroke: black;
    stroke-width: 10px;
    stroke-linejoin: round;
    stroke-linecap: round; }

.project__image {
  max-width: 600px;
  width: 100%;
  -webkit-transform: translateX(-150vw);
          transform: translateX(-150vw); }

.project .curly {
  stroke-dasharray: 1310px;
  stroke: black;
  stroke-width: 1px;
  stroke-linejoin: round;
  stroke-linecap: round; }

.loading {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: white;
  z-index: 9999; }
  .loading svg {
    width: 150px;
    height: auto;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .loading #loading1 {
    stroke-dasharray: 719px;
    stroke-dashoffset: 719px;
    -webkit-animation: twirl 1.5s ease forwards;
            animation: twirl 1.5s ease forwards; }
  .loading #loading2 {
    stroke-dasharray: 423px;
    stroke-dashoffset: 423px;
    -webkit-animation: twirl 1.5s ease .1s forwards;
            animation: twirl 1.5s ease .1s forwards; }
  .loading #loading3 {
    stroke-dasharray: 226px;
    stroke-dashoffset: 226px;
    -webkit-animation: twirl 1.5s ease .2s forwards;
            animation: twirl 1.5s ease .2s forwards; }
  .loading #loading4 {
    stroke-dasharray: 291px;
    stroke-dashoffset: 291px;
    -webkit-animation: twirl 1.5s ease .3s forwards;
            animation: twirl 1.5s ease .3s forwards; }
  .loading #loading5 {
    stroke-dasharray: 415px;
    stroke-dashoffset: 415px;
    -webkit-animation: twirl 1.5s ease .4s forwards;
            animation: twirl 1.5s ease .4s forwards; }
  .loading #loading6 {
    stroke-dasharray: 588px;
    stroke-dashoffset: 588px;
    -webkit-animation: twirl 1.5s ease .5s forwards;
            animation: twirl 1.5s ease .5s forwards; }
  .loading #loading7 {
    stroke-dasharray: 423px;
    stroke-dashoffset: 423px;
    -webkit-animation: twirl 1.5s ease .6s forwards;
            animation: twirl 1.5s ease .6s forwards; }
  .loading #loading1,
  .loading #loading2,
  .loading #loading3,
  .loading #loading4,
  .loading #loading5,
  .loading #loading6,
  .loading #loading7 {
    stroke: black;
    stroke-width: 10px;
    stroke-linejoin: round;
    stroke-linecap: round; }
  .loading.fade-out {
    -webkit-animation: hide .5s ease forwards;
            animation: hide .5s ease forwards; }

@-webkit-keyframes hide {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes hide {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@-webkit-keyframes twirl {
  50% {
    stroke-dashoffset: 0; }
  100% {
    opacity: 0; } }

@keyframes twirl {
  50% {
    stroke-dashoffset: 0; }
  100% {
    opacity: 0; } }


